.homeInfo {
  display: flex;
  flex-direction: column;
  .info {
    h2 {
      text-transform: uppercase;
      font-weight: bold;
    }
  }
}

@media (min-width: 700px) {
  .homeInfo {
    width: 50%;
    align-items: center;
    min-height: 70vh;

    .homeItemLogo {
      display: none;
    }
    .info {
      width: 140%;
      margin: 0 50% 40px 0;
      min-height: 70vh;
      padding: 10px 20% 10px 60%;
      box-shadow: 0px 0px 17px 0px rgba(66, 68, 90, 1);
      border-radius: 15px;
      background: rgb(255, 255, 255);
      background: linear-gradient(
        90deg,
        rgba(255, 255, 255, 1) 0%,
        rgba(175, 203, 31, 0.3) 100%
      );
      h2 {
        font-size: 30px;
      }
      h3 {
        margin-top: 20px;
      }
      p {
        margin-top: 15px;
      }
    }
  }
}

@media (max-width: 700px) {
  .homeInfo {
    width: 100%;
    height: 100%;
    align-items: center;
    img {
      width: 80%;
    }
    .info {
      width: 100%;
      height: 30%;
      display: flex;
      flex-direction: column;
      span {
        font-size: 20px;
        text-align: center;
        letter-spacing: 0.1em;
        background-color: rgba(215, 143, 10, 0.732);
        padding: 25px;
        border-radius: 15px;
        color: white;
        margin: 10px 10px;
      }
    }
  }
}
